import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { LoginComponent } from './login.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { SigninComponent } from './signin/signin.component';
import { SignupComponent } from './signup/signup.component';
import { ModalSuccessComponent } from './modal-success/modal-success.component';
import { VhComponent } from '../sales/components/vh-component/vh-component';


const routes: Routes = [
    {
        path: '',
        component: LoginComponent,
        children: [
            {
                path: '',
                component: SigninComponent,
            },
            {
                path: 'signup',
                component: SignupComponent,
            }
        ]
    },
]
@NgModule({
    declarations: [LoginComponent, SigninComponent, SignupComponent, ModalSuccessComponent],
    imports: [
        CommonModule,
        IonicModule,
        FormsModule,
        ReactiveFormsModule,
        NgZorroAntModule,
        TranslateModule,
        RouterModule.forChild(routes)
    ],
    entryComponents: [ModalSuccessComponent],
    providers:[VhComponent]
})
export class LoginModule { }
